.note {
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  /* width: 360px; */
  margin: 16px;
  float: left;
}
.note h1 {
  font-size: 1.1em;
  margin-bottom: 6px;
}
.note p {
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.btn {
  background-color: #3366ff;
  border-style: dashed;
  position: center;
  margin-left: 48%;
}
.header {
  background-color: #6495ed;
  margin: 0px;
  font-family: sans-serif;
  text-align: center;
}

.header h1 {
  padding: 3%;
}
